/* font */
@font-face{
  font-family: "juice";
  src: url("../assets/fonts/orange-juice/orange\ juice\ 2.0.ttf") format('truetype');
}

/* global */
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  background: rgb(196, 227, 3, 0.1);
  position: relative;
}

code{
  margin: 5% auto;
  display: block;
  font-size: 18px;
  color: #555;
}

.logo{
  max-width: 100px;
  max-height: 100px;
}

/* error */
.error-message{
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 35%;
  left: 20%;
  width: 60%;
  height: 30%;
  border: 10px double red;
  box-shadow: 0 0 10px #000000;
  border-radius: 20px;
  background: #FFBABA;
  font-size: 2rem;
  font-family: "Josefin Sans", sans-serif;
  color: #ffffff;
  line-height: 2;
}

.warning-message{
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 35%;
  left: 20%;
  width: 55%;
  height: 40%;
  border: 10px double goldenrod;
  box-shadow: 0 0 10px #000000;
  border-radius: 20px;
  background: rgb(248, 217, 138);
  font-size: 2rem;
  font-family: "Josefin Sans", sans-serif;
  color: #ffffff;
  line-height: 2;
}

.error-icon{
  max-width: 100px;
  max-height: 100px;
}

.warning-icon{
  max-width: 100px;
  max-height: 100px;
}

/* media query */
@media (max-width: 400px){
  .logo{
    max-width: 50px;
    max-height: 50px;
  }
  /* error */
  .error-message{
    top: 60%;
    left: 5%;
    width: 95%;
    height: 100%;
  }
  .warning-message{
    top: 60%;
    left: 5%;
    width: 95%;
    height: 100%;
    line-height: 1.5;
    font-size: 1.2rem;
  }
  .warning-icon{
    max-width: 40px;
    max-height: 40px;
  }
}

@media (min-width: 401px) and (max-width: 500px){
  .logo{
    max-width: 50px;
    max-height: 50px;
  }
  /* error */
  .error-message{
    top: 60%;
    left: 5%;
    width: 90%;
    height: 60%;
  }
  .warning-message{
    top: 60%;
    left: 5%;
    width: 90%;
    height: 60%;
    line-height: 1.5;
    font-size: 1.2rem;
  }
}

@media (min-width: 501px) and (max-width: 750px){
  .logo{
    max-width: 50px;
    max-height: 50px;
  }
  /* error */
  .error-message{
    top: 65%;
    left: 5%;
    width: 90%;
    height: 50%;
  }
  .warning-message{
    top: 65%;
    left: 5%;
    width: 90%;
    height: 60%;
    line-height: 1.5;
    font-size: 1.5rem;
  }
}
@media (min-width: 751px) and (max-width: 999px){
  /* error */
  .error-message{
    top: 40%;
    left: 5%;
    width: 90%;
  }
  .warning-message{
    top: 40%;
    left: 5%;
    width: 90%;
    height: 50%;
    line-height: 1.5;
    font-size: 1.9rem;
  }
}

@media (min-width: 1000px) and (max-width: 1299px){
  /* error */
  .error-message{
    top: 40%;
    left: 20%;
  }
  .warning-message{
    top: 40%;
    left: 20%;
    height: 50%;
    line-height: 1.5;
    font-size: 1.9rem;
  }
}

@media (min-width: 1300px) and (max-width: 1700px){
  /* error */
  .error-message{
    left: 25%;
  }
  .warning-message{
    left: 25%;
    height: 50%;
    line-height: 1.5;
    font-size: 1.9rem;
  }
}